"use client";

import Link from "next/link";
import { usePathname, useRouter } from "next/navigation";
import { useAuthStore } from "../store/authStore";
import { useState } from "react";
import { LogOut, User, Menu, X, BookOpen, BarChart2, GitBranch, Brain } from "lucide-react";

export default function Navbar() {
    const { isAuthenticated, user, logout } = useAuthStore();
    const pathname = usePathname();
    const router = useRouter();
    const [mobileOpen, setMobileOpen] = useState(false);
    const [userMenuOpen, setUserMenuOpen] = useState(false);

    const handleLogout = () => {
        logout();
        router.push("/");
    };

    const navLinks = isAuthenticated
        ? [
            { href: "/dashboard", label: "Dashboard", icon: BarChart2 },
            { href: "/problems", label: "Problems", icon: BookOpen },
            { href: "/graph", label: "Graph", icon: GitBranch },
            { href: "/profile", label: "Profile", icon: User },
        ]
        : [
            { href: "#features", label: "Features" },
            { href: "#how-it-works", label: "How It Works" },
        ];

    return (
        <nav className="fixed top-0 left-0 right-0 z-50 bg-[#faf9f6]/80 backdrop-blur-md border-b border-[#e5e7eb]">
            <div className="max-w-7xl mx-auto px-6 flex items-center justify-between h-16">
                {/* Logo */}
                <Link href="/" className="flex items-center gap-2 no-underline">
                    <div className="flex gap-1">
                        <div className="w-2.5 h-2.5 bg-[#f97316] rounded-sm" />
                        <div className="w-2.5 h-2.5 bg-[#0d0d0d] rounded-sm" />
                    </div>
                    <span className="font-serif font-bold text-lg tracking-tight text-[#0d0d0d]">
                        Perspectra
                        <span className="text-[#f97316]">.</span>
                    </span>
                </Link>

                {/* Desktop Nav */}
                <div className="hidden md:flex items-center gap-6">
                    {navLinks.map((link) => (
                        <Link
                            key={link.href}
                            href={link.href}
                            className={`text-sm font-medium transition-colors no-underline ${pathname === link.href
                                    ? "text-[#f97316]"
                                    : "text-[#6b7280] hover:text-[#0d0d0d]"
                                }`}
                        >
                            {link.label}
                        </Link>
                    ))}
                </div>

                {/* Right side */}
                <div className="hidden md:flex items-center gap-3">
                    {isAuthenticated ? (
                        <div className="relative">
                            <button
                                onClick={() => setUserMenuOpen(!userMenuOpen)}
                                className="flex items-center gap-2 text-sm font-medium text-[#0d0d0d] bg-white border border-[#e5e7eb] rounded-full px-3 py-1.5 hover:border-[#f97316] transition-colors"
                            >
                                <div className="w-6 h-6 bg-[#f97316] rounded-full flex items-center justify-center text-white text-xs font-bold">
                                    {user?.name?.[0]?.toUpperCase() || "U"}
                                </div>
                                <span>{user?.name?.split(" ")[0]}</span>
                            </button>
                            {userMenuOpen && (
                                <div className="absolute right-0 top-10 w-44 bg-white border border-[#e5e7eb] rounded-lg shadow-xl py-1 z-50">
                                    <button
                                        onClick={handleLogout}
                                        className="w-full flex items-center gap-2 px-4 py-2.5 text-sm text-[#ef4444] hover:bg-red-50 transition-colors"
                                    >
                                        <LogOut className="w-4 h-4" /> Sign Out
                                    </button>
                                </div>
                            )}
                        </div>
                    ) : (
                        <>
                            <Link href="/login" className="btn-outline py-2 px-5 text-xs">
                                Log In
                            </Link>
                            <Link href="/register" className="btn-accent py-2 px-5 text-xs">
                                Get Access
                            </Link>
                        </>
                    )}
                </div>

                {/* Mobile burger */}
                <button
                    className="md:hidden text-[#0d0d0d]"
                    onClick={() => setMobileOpen(!mobileOpen)}
                >
                    {mobileOpen ? <X className="w-5 h-5" /> : <Menu className="w-5 h-5" />}
                </button>
            </div>

            {/* Mobile menu */}
            {mobileOpen && (
                <div className="md:hidden bg-[#faf9f6] border-t border-[#e5e7eb] px-6 py-4 flex flex-col gap-3">
                    {navLinks.map((link) => (
                        <Link
                            key={link.href}
                            href={link.href}
                            className="text-sm font-medium text-[#0d0d0d] no-underline py-2"
                            onClick={() => setMobileOpen(false)}
                        >
                            {link.label}
                        </Link>
                    ))}
                    {isAuthenticated ? (
                        <button
                            onClick={handleLogout}
                            className="text-left text-sm text-[#ef4444] py-2"
                        >
                            Sign Out
                        </button>
                    ) : (
                        <>
                            <Link href="/login" className="btn-outline text-center py-2">Log In</Link>
                            <Link href="/register" className="btn-accent text-center py-2">Get Access</Link>
                        </>
                    )}
                </div>
            )}
        </nav>
    );
}
