import { create } from "zustand";
import { User } from "../lib/types";

interface AuthState {
    user: User | null;
    token: string | null;
    isAuthenticated: boolean;
    login: (token: string, user: User) => void;
    logout: () => void;
    initAuth: () => void;
}

export const useAuthStore = create<AuthState>((set) => ({
    user: null,
    token: null,
    isAuthenticated: false,

    login: (token, user) => {
        localStorage.setItem("perspectra_token", token);
        localStorage.setItem("perspectra_user", JSON.stringify(user));
        set({ token, user, isAuthenticated: true });
    },

    logout: () => {
        localStorage.removeItem("perspectra_token");
        localStorage.removeItem("perspectra_user");
        set({ token: null, user: null, isAuthenticated: false });
    },

    initAuth: () => {
        const token = localStorage.getItem("perspectra_token");
        const userStr = localStorage.getItem("perspectra_user");
        if (token && userStr) {
            try {
                const user = JSON.parse(userStr) as User;
                set({ token, user, isAuthenticated: true });
            } catch {
                set({ token: null, user: null, isAuthenticated: false });
            }
        }
    },
}));
